import NiceModal, { useModal, antdModal } from "@ebay/nice-modal-react";
import { Form, Input, Modal, Select, Spin } from "antd";
import { DADATA_API } from "features/dadata";
import { useCallback, useState } from "react";
import { ICompany } from "./types";

const { Option } = Select;

export default NiceModal.create(({ company }: { company: ICompany }) => {
  const [options, setOptions] = useState<any[]>([]);

  const modal = useModal();
  const [form] = Form.useForm();

  const onSearch = async (value: string) => {
    const newOptions = await DADATA_API.getCompany(value);
    setOptions(newOptions);
  };

  const handleSubmit = useCallback(() => {
    form.validateFields().then(() => {
      const newCompany = { ...form.getFieldsValue() };
      modal.resolve(newCompany);
      modal.hide();
    });
  }, [
    modal,
    // user,
    form,
  ]);

  return (
    <Modal
      {...antdModal(modal)}
      title="–°–æ–∑–¥–∞—Ç—å –∫–æ–º–ø–∞–Ω–∏—é"
      okText="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
      cancelText="–û—Ç–º–µ–Ω–∞"
      onOk={handleSubmit}
    >
      <Form form={form} layout="vertical">
        <Form.Item
          name="inn"
          label="–ù–∞–∑–≤–∞–Ω–∏–µ –∏–ª–∏ –ò–ù–ù –∫–æ–º–ø–∞–Ω–∏–∏"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Select
            showSearch
            filterOption={false}
            onSearch={onSearch}
            // notFoundContent={fetching ? <Spin size="small" /> : null}
          >
            {options.map((option) => (
              <Option value={option.inn}>
                <span>{option.name} </span>
                <span style={{ color: "gray", fontSize: "10px" }}>
                  {option.inn}
                </span>
              </Option>
            ))}
          </Select>
        </Form.Item>
      </Form>
    </Modal>
  );
});
